*, ::after, ::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  font-family: "Roboto", sans-serif;
  transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1);
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: 100%;
}

i {
  width: 2rem;
  margin-right: 2rem;
}

.contenido {
  position: absolute;
  width: calc(100% - 8rem);
  min-height: calc(100vh - 8rem);
  top: 8rem;
  left: 8rem;
  transition: all 0.3s ease-in-out;
  padding-top: 5rem;
}
.contenido.redimensionar {
  left: 25rem;
  width: calc(100% - 25rem);
}
@media (max-width: 768px) {
  .contenido {
    left: 0;
    width: 100%;
  }
}
@media (max-width: 480px) {
  .contenido.redimensionar {
    left: 0;
    width: 100%;
  }
}
.contenido #cont-paginas {
  height: 100%;
  margin-bottom: 10rem;
}
.contenido #cont-paginas .dinamico {
  width: 100%;
  height: 100%;
  border: none;
}

.introducciones {
  width: 100%;
  height: 60rem;
  overflow: hidden;
  position: relative;
}
@media (max-width: 480px) {
  .introducciones {
    height: 80vh;
  }
}